import back from '@/components/back'
import searchCustom from '~/Pharmacy/searchCustom'
export default {
	data () {
		return {
			title: '',
			empgasisNumber: 0,
			placeholder: '请输入患者相关信息',
			arr: [],
			page: 1,
			size: 15,
			search: '',
			listStart: true,
			start404: false,
			start1: true,
			start2: true,
			start3: true,
			haveMore: true,
			PatientName: '',
			show: false,
			Total: '',
			DrId: this.$store.state.userInfo == null ? 2 : this.$store.state.userInfo.Data.User.DoctorId,
			operation:false,
			operationText:'编辑',
			showConfirm:false,
			pPatientId:'',
			name:'',
			MFItemId:"",
			MFItemArr:[],
		}
	},
	components:{
		searchCustom,
		back
	},
	methods: {
		operationList(){
			let grouapsArr = this.$store.state.grouapsArr
			this.MFItemId = grouapsArr.MFItemId 
			let PatientIds = []
			this.MFItemArr.forEach(v =>{
				PatientIds.push(v.PatientId) 
			})
			if(PatientIds.length == 0){
				return
			}
			this.$post('PlatFormAPI/Memberfeatures/BacthMemberfeaturesPatient',{
			    DrId: this.DrId,
			    MFItemId:grouapsArr.MFItemId ,
				MFItemName:grouapsArr.MFItemName,
				PatientIds:PatientIds
			}).then(o => {
				if(o){
					this.$router.back()
				}
			})
		},
		patientSex (start){
			switch (start) {
				case 1:
					return '男'
					break;
				case 2:
					return '女'
					break;
				case 3:
					return '未知'
					break;		
			}
		},
		searchFor(){
			this.page = 1
			this.haveMore = true
			this.httpStart()
		},
		httpStart (b) { 
			if(!this.haveMore){
				return
			}
			let obj = {};
			let grouapsArr = this.$store.state.grouapsArr
			this.MFItemId = grouapsArr.MFItemId 
			if(grouapsArr.MFItemName != undefined){
				this.title = grouapsArr.MFItemName;
			}else{
				this.title = '';
			}
			obj.url = '/PlatFormAPI/DoctorPatient/QueryDoctorPatientMemberfuresByPage'
			obj.parameter = {
				DrId: grouapsArr.DrId,
				MFItemId: grouapsArr.MFItemId,
				PatientName: this.search,
				"PageIndex": this.page,
				"PageSize": this.size
			}
			this.$post(obj.url,obj.parameter).then(o => {
				if(o.ReturnData == ''  || o.ReturnData == [] || o.ReturnData == undefined){
					this.start404 = true
				}else{
					this.start404 = false
				}
				if (!o.ReturnData || o.ReturnData.length < 10){
					this.haveMore = false
				}
				else {
					this.haveMore = true
				}
				if (b){
					o.ReturnData.forEach(v => {
						this.arr.push(v)
					})
				}
				else {
					this.arr = o.ReturnData
				}
				this.Total = o.Total
				
				if(o){
					this.arr.map((v,n) => {
						this.arr[n].checkedNames = 'circle'
					})
					if(this.MFItemArr.length > 0){
						this.arr.map((v,n) => {
							this.MFItemArr.map((w,i) => {
								if(v.PatientId == w.PatientId){
									this.arr[n].checkedNames = 'success'
								}
							})
						})
					}
				}
				
			
			})
		},
		toArc(n,v){
			this.listStart = false
			if(this.arr[n].checkedNames == 'circle'){
				this.MFItemArr.push(v)
				this.arr[n].checkedNames = 'success'
			}else{
				this.MFItemArr.map((w,i) => {
					if(v.PatientId == w.PatientId){
						this.MFItemArr.splice(i, 1)	
					}
				})
				this.arr[n].checkedNames = 'circle'
			}
			this.listStart = true
			console.log(this.MFItemArr)
		},
		getMore(){ //下拉加载分页
			const el = this.$refs.el
			let timer = null
			window.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = document.documentElement.scrollTop
					|| window.pageYOffset
					|| document.body.scrollTop,
					h = el.scrollHeight,
					wh = window.innerHeight
					if (h - top - wh < 100 && this.haveMore){
						if(this.Total > this.size){
							this.page += 1
							this.httpStart(true)
						}
					}
				}, 100)
			}
		}
	},
	mounted () {
		//this.httpStart(this.$route.params.start);
		let _this = this
		_this.getMore();
		_this.$bridge.registerhandler('em', (data, responseCallback) => {
			_this.httpStart(_this.$route.params.start);
			responseCallback('接收到数据')
		})
	},
	beforeDestroy(){
		window.onscroll = null
	},
	created(){
		this.httpStart();
	}
}



